.ait-dropdown {
    position: relative;
}

.ait-dropdown .ait-dropdown-toggle {
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.ait-dropdown > input[type=checkbox],
.ait-dropdown .ait-dropdown-content {
    display: none;
}

.ait-dropdown input[type=checkbox]:checked ~ .ait-dropdown-content {
    display: block;
    position: absolute;
}

.ait-dropdown--floating input[type=checkbox]:checked ~ .ait-dropdown-content {
    -webkit-transform: translateX(-100%);
        -ms-transform: translateX(-100%);
            transform: translateX(-100%);
    left: 100%;
}


