.ait {
  background-color: var(--bg-darkest);
  color: white;
  height: 100%;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
}

.ait-container {
  height: 100%;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.ait-body {
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  padding: 0 4rem 1rem;
  overflow-y: auto;
  height: 100%;
}

.ait-header,
.ait-footer {
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
}

.ait-header {
  font-size: 1.2rem;
  padding: 1.3rem 4rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  border-bottom: 1px solid var(--border-dark);
}

@media screen and (max-width: 767px) {
  .ait-header {
    padding: 1.3rem 1rem;
  }

  .ait-body {
    padding: 0 1rem;
  }
}

.ait-footer {
  display: -ms-flexbox;
  display: flex;
}

.ait-title {
  color: white;
  text-decoration: none;
  display: block;
}


